html, body {
  -webkit-text-size-adjust: 100%;
  overscroll-behavior: none;
  background: #000;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: ui-sans-serif, system-ui, -apple-system, Arial;
  overflow: hidden;
}

html {
  touch-action: manipulation;
  position: fixed;
  inset: 0;
}

body {
  position: fixed;
  inset: 0;
}

#app-root {
  z-index: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  inset: 0;
}

#ui {
  color: #fff;
  background: radial-gradient(#000000b3, #000000e6);
  place-items: center;
  font-family: ui-sans-serif, system-ui, -apple-system, Arial;
  display: grid;
  position: fixed;
  inset: 0;
}

#ui .panel {
  text-align: center;
  background: #00000040;
  border: 1px solid #fff3;
  border-radius: 12px;
  gap: 12px;
  padding: 18px 22px;
  display: grid;
}

button {
  appearance: none;
  color: #111;
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 10px;
  padding: 12px 16px;
  font-size: 16px;
}

.hidden {
  display: none !important;
}

#hud {
  z-index: 10;
  flex-direction: column;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

#hud-drawer {
  flex-direction: column;
  align-items: center;
  display: flex;
  overflow: hidden;
}

#hud-drawer-handle {
  color: #fff;
  cursor: pointer;
  opacity: .9;
  background: #00000080;
  border: 1px solid #fff3;
  border-bottom-width: 0;
  border-radius: 12px 12px 0 0;
  flex-shrink: 0;
  width: 64px;
  min-height: 48px;
  padding: 12px 0;
  font-size: 14px;
}

#hud-drawer-handle:hover {
  background: #000000b3;
}

#hud-drawer-handle:after {
  content: "▲";
}

#hud-drawer.collapsed #hud-drawer-handle:after {
  content: "▼";
}

#hud-drawer-content {
  background: #00000080;
  border: 1px solid #fff3;
  border-bottom: none;
  border-radius: 12px 12px 0 0;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  max-width: 100%;
  max-height: 280px;
  padding: 10px 12px 12px;
  transition: max-height .3s ease-out, padding .3s ease-out, opacity .3s ease-out;
  display: flex;
  overflow: hidden;
}

.hud-row {
  flex-wrap: wrap;
  justify-content: center;
  gap: 8px;
  display: flex;
}

.hud-row-opacity {
  align-items: center;
}

.hud-label {
  color: #ffffffe6;
  font-size: 14px;
}

.hud-checkbox-label {
  color: #111;
  cursor: pointer;
  background: #ffffffd9;
  border-radius: 10px;
  align-items: center;
  gap: 8px;
  padding: 6px 10px 6px 6px;
  font-size: 14px;
  display: flex;
}

#bg-opacity-slider {
  accent-color: #fff9;
  width: 120px;
}

#hud-drawer.collapsed #hud-drawer-content {
  opacity: 0;
  pointer-events: none;
  max-height: 0;
  padding-top: 0;
  padding-bottom: 0;
}

#hud button {
  opacity: .85;
}

#hud button.hud-toggle {
  color: #fffc;
  background: #fff3;
  border: 1px solid #ffffff4d;
}

.video-btn-group {
  border-radius: 10px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 0 1px #fff6;
}

.video-btn-group .video-btn {
  border: none;
  border-right: 1px solid #0000001f;
  border-radius: 0;
}

.video-btn-group .video-btn:first-child {
  border-radius: 10px 0 0 10px;
}

.video-btn-group .video-btn:last-child {
  border-right: none;
  border-radius: 0 10px 10px 0;
}

#hud button.hud-toggle.active {
  color: #fff;
  background: #4a9e4ae6;
  border-color: #ffffff80;
}

canvas.webgl {
  touch-action: none;
  z-index: 0;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  inset: 0;
}

#ui, #hud {
  z-index: 10;
}

#vr-landscape-overlay {
  color: #fff;
  z-index: 15;
  background: radial-gradient(#000000b3, #000000e6);
  place-items: center;
  font-family: ui-sans-serif, system-ui, -apple-system, Arial;
  display: grid;
  position: fixed;
  inset: 0;
}

#vr-landscape-overlay[hidden] {
  display: none !important;
}

#vr-landscape-overlay .panel {
  padding: 50px;
}

#add-to-homescreen-tip {
  color: #fff;
  z-index: 20;
  background: #000000d9;
  border: 1px solid #ffffff40;
  border-radius: 12px;
  max-width: 320px;
  padding: 14px 16px;
  font-family: ui-sans-serif, system-ui, -apple-system, Arial;
  font-size: 14px;
  line-height: 1.4;
  display: none;
  position: fixed;
  bottom: 80px;
  left: auto;
  right: 12px;
  box-shadow: 0 4px 20px #0006;
}

#add-to-homescreen-tip.visible {
  display: block;
}

#add-to-homescreen-tip .tip-header {
  justify-content: space-between;
  align-items: flex-start;
  gap: 8px;
  margin-bottom: 8px;
  display: flex;
}

#add-to-homescreen-tip .tip-title {
  font-size: 15px;
  font-weight: 600;
}

#add-to-homescreen-tip .tip-close {
  color: #fff;
  cursor: pointer;
  background: #ffffff26;
  border: none;
  border-radius: 6px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  padding: 0;
  font-size: 18px;
  line-height: 1;
  display: flex;
}

#add-to-homescreen-tip .tip-close:hover {
  background: #ffffff40;
}

#add-to-homescreen-tip .tip-body {
  opacity: .95;
  font-size: 13px;
}

.qr-overlay {
  z-index: 100;
  background: #000;
  flex-direction: column;
  display: flex;
  position: fixed;
  inset: 0;
  overflow: hidden;
}

#qr-scanner {
  margin-top: 0;
}

.qr-overlay[hidden], #qr-scanner[hidden] {
  display: none !important;
}

.qr-overlay-header {
  color: #fff;
  background: #000c;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
  padding: 12px 16px;
  font-family: ui-sans-serif, system-ui, -apple-system, Arial;
  display: flex;
}

.qr-overlay-header-text {
  flex-direction: column;
  gap: 4px;
  display: flex;
}

.qr-overlay-header-hint {
  opacity: .9;
  font-size: 12px;
}

.qr-overlay-header-hint a {
  color: #7eb8ff;
  text-decoration: underline;
}

.qr-close {
  color: #fff;
  cursor: pointer;
  background: #fff3;
  border: none;
  border-radius: 8px;
  width: 36px;
  height: 36px;
  padding: 0;
  font-size: 24px;
  line-height: 1;
}

#qr-video {
  object-fit: cover;
  flex: 1;
  width: 100%;
  min-height: 0;
}

.qr-overlay-footer {
  background: #000c;
  flex-wrap: wrap;
  flex-shrink: 0;
  justify-content: center;
  gap: 12px;
  padding: 12px;
  display: flex;
}

.qr-clear-btn {
  color: #ffffffb3;
  background: none;
  padding: 8px 12px;
  font-size: 13px;
}

.ios-icon {
  vertical-align: -.15em;
  fill: currentColor;
  width: 1.05em;
  height: 1.05em;
  margin: 0 .15em;
}

.ios-icon.stroke {
  fill: none;
  stroke: currentColor;
  stroke-width: 2px;
  stroke-linecap: round;
  stroke-linejoin: round;
}

li {
  margin-bottom: 14px;
}

input[type="checkbox"] {
  width: 20px;
  height: 20px;
}
/*# sourceMappingURL=CardboardVR_experiment.8c882b89.css.map */
